import React, { useState, useEffect } from 'react'
import { QRCode } from 'react-qrcode-logo'
import styled from 'styled-components'
import { CardFooter, Flex, Input, Skeleton, Text } from '../../uikit'
import { useAppDispatch } from '../../state'
import CopyToClipboard from '../../uikit/widgets/WalletModal/CopyToClipboard'
import { checkTxThunk, createTxThunk } from '../../state/user/reducer'
import { useAuthentication, useFiat, useTx, useTxPayed } from '../../state/user/hooks'
import { Loading } from '../components/Loading'
import { Timer } from '../components/Timer'
import { useTranslation } from '../../contexts/Localization'

const StyledInput = styled(Input)`
  box-shadow: none;
  width: 100%;
  margin: 0 8px;
  padding: 0 8px;
  text-align: center;
`

export const Payment: React.FC<{ tx: string; clientId: string }> = ({ tx, clientId }) => {
  const dispatch = useAppDispatch()
  const { t } = useTranslation()
  const txData = useTx()
  const auth = useAuthentication()
  const { tx: transactionValidated } = useTxPayed()
  const fiat = useFiat()

  useEffect(() => {
    if (txData === undefined) {
      dispatch(createTxThunk({ tx, clientId }))
    }
  }, [dispatch, tx, clientId, txData])

  if (txData === undefined || txData == null) {
    return (
      <CardFooter>
        <Flex justifyContent="center" alignContent="center" alignItems="center">
          <Loading />
        </Flex>
      </CardFooter>
    )
  }
  return (
    <CardFooter>
      <Flex flexDirection="row" mb="8px" justifyContent="space-between">
        <Flex flexDirection="column">
          <Text color="textSubtle">Amount</Text>
          <Text bold fontSize="26px">
            SLP {txData.amount}
            {fiat && (
              <Text color="success" fontSize="21px" ml="9px" as="span">
                 {(fiat * txData.amount).toFixed(2)}
              </Text>
            )}
            {fiat == null && <Skeleton />}
          </Text>
        </Flex>
        <Flex alignItems="flex-end" flexDirection="column">
          <Text color="textSubtle">Time remaining</Text>
          <Timer />
        </Flex>
      </Flex>

      <Flex mb="16px" flexDirection="column" justifyContent="center" alignContent="center" alignItems="center">
        <Text color="textSubtle" fontSize="18px" mt="16px">
          {t(
            'Please make sure you make the payment form this address, as we  can only validate payments from your validated address.',
          )}
        </Text>
        <Text color="warning" fontSize="initial" mb="16px" style={{ userSelect: 'none' }}>
          {auth.ronin}
        </Text>
        <Text color="textSubtle" fontSize="18px">
          Ronin mainnet
        </Text>
        <QRCode size={300} value={txData.ronin} logoImage="/images/ronin_icon.png" />
      </Flex>
      <CopyToClipboard successMsg="" toCopy={txData.ronin}>
        <StyledInput readOnly value={txData.ronin} />
      </CopyToClipboard>
    </CardFooter>
  )
}
