import React from 'react'
import { Button, CardFooter, Flex, Text } from '../../uikit'
import { useTranslation } from '../../contexts/Localization'
import { useTx } from '../../state/user/hooks'
import { useAppDispatch } from '../../state'

export const TimeOver: React.FC = () => {
  const { t } = useTranslation()
  const txResponse = useTx()

  return (
    <>
      <CardFooter>
        <Flex flexDirection="column">
          <Text color="warning" fontSize="18px" as="span">
            {t(' We could not validate the payment!\n ')}
          </Text>
          <Text color="textSubtle" mb="16px" fontSize="21px" as="span">
            {t('Start the payment again and make sure of:\n')}
          </Text>
          <Text color="textSubtle" mb="16px" fontSize="21px" as="span">
            {t('You did the payment from your validated address, and to the pay to address')}
          </Text>
          <Text color="textSubtle" mb="16px" fontSize="21px" as="span">
            {t('The amount payed matches the amount showed')}
          </Text>
          <Text color="textSubtle" mb="16px" fontSize="21px" as="span">
            {t('Sometimes the Ronin network is slow, try in a few seconds.')}
          </Text>
        </Flex>
      </CardFooter>
      <CardFooter>
        <Flex flexDirection="row" justifyContent="center">
          <Button
            mt="16px"
            variant="danger"
            as="a"
            scale="md"
            onClick={() => {
              if (txResponse !== undefined) {
                window.location.href = txResponse.failureURL
              } else {
                window.location.href = 'https://tracker.axie.management'
              }
            }}
          >
            {t('Expired ')}
          </Button>
        </Flex>
      </CardFooter>
    </>
  )
}
