import React from 'react'
import styled from 'styled-components'
import { Button } from '../../uikit'
import { useTranslation } from '../../contexts/Localization'

interface ValidateButtonProps {
  onValidate: () => void
  validate: boolean
}

const MiniDots = styled.div`
  position: relative;
  left: -9988px;
  bottom: -6px;
  width: 3px;
  height: 3px;
  border-radius: 1px;
  background-color: #666171;
  color: #666171;
  box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
  animation: minDotTyping 1.5s infinite linear;

  @keyframes minDotTyping {
    0% {
      box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
    }
    16.667% {
      box-shadow: 9990px -10px 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
    }
    33.333% {
      box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
    }
    50% {
      box-shadow: 9990px 0 0 0 #666171, 9995px -10px 0 0 #666171, 10000px 0 0 0 #666171;
    }
    66.667% {
      box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
    }
    83.333% {
      box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px -10px 0 0 #666171;
    }
    100% {
      box-shadow: 9990px 0 0 0 #666171, 9995px 0 0 0 #666171, 10000px 0 0 0 #666171;
    }
  }
`

export const ValidateButton: React.FC<ValidateButtonProps> = ({ validate, onValidate }) => {
  const { t } = useTranslation()

  return (
    <Button
      mt="16px"
      as="a"
      scale="md"
      disabled={validate}
      onClick={() => {
        onValidate()
      }}
    >
      {validate && (
        <>
          {t('Validating ')}
          <MiniDots />
        </>
      )}
      {!validate && t('Validate')}
    </Button>
  )
}
