import React from 'react'
import { useLoadings } from '../../state/user/hooks'
import { useAppDispatch } from '../../state'
import { ValidateButton } from './ValidateButton'
import { validateThunk } from '../../state/user/reducer'

interface ValidateTxButtonProps {
  tx: string
  clientId: string
}

export const ValidateAuthButton: React.FC<ValidateTxButtonProps> = ({ tx, clientId }) => {
  const dispatch = useAppDispatch()
  const { validateLoading } = useLoadings()

  return <ValidateButton onValidate={() => dispatch(validateThunk({ tx, clientId }))} validate={validateLoading} />
}
