import React, { useEffect, useState } from 'react'
import { CardFooter, Flex, Input, LinkExternal, Text } from '../../uikit'
import { Done } from '../components/Done'
import { useTranslation } from '../../contexts/Localization'
import { useTx, useTxPayed } from '../../state/user/hooks'

export const Paid: React.FC<{ txHash: string }> = ({ txHash }) => {
  const { t } = useTranslation()
  const [second, setSecond] = useState(5)
  const [counter, setCounter] = useState(second)
  const tx = useTx()
  const { payed } = useTxPayed()
  useEffect(() => {
    let intervalId
    if (counter >= 0) {
      intervalId = setInterval(() => {
        const secondCounter = (counter % 60).toFixed()

        const computedSecond = String(secondCounter).length === 1 ? `0${secondCounter}` : secondCounter.toString()
        setSecond(parseInt(computedSecond))

        setCounter((_counter) => _counter - 1)
      }, 1000)
    } else {
      window.location.href = tx.succeedURL
    }

    return () => clearInterval(intervalId)
  }, [counter, tx])

  return (
    <>
      <CardFooter>
        <Flex flexDirection="column" justifyContent="center" alignContent="center" alignItems="center">
          <Text color="textSubtle" fontSize="18px" mb="12px">
            {t('Congratulations payment done  ')}
          </Text>
          <Done />
          <Text color="textSubtle" fontSize="18px" mb="12px">
            {t('You can check the transaction in ronin explorer')}
          </Text>
          <LinkExternal small href={`https://explorer.roninchain.com/tx/${txHash}`} mr="16px">
            View on RoninExplorer
          </LinkExternal>
        </Flex>
      </CardFooter>
      <CardFooter>
        <Text color="success">{t(`Redirecting in ${second} seconds`)}</Text>
      </CardFooter>
    </>
  )
}
