import React, { useState, useEffect } from 'react'
import { useAuthentication, useLoadings, useTimeDone, useTx, useTxPayed } from '../../state/user/hooks'
import { useAppDispatch } from '../../state'
import { ValidateButton } from './ValidateButton'
import { checkTxThunk } from '../../state/user/reducer'

export const ValidateTxButton: React.FC<{ tx: string; clientId: string }> = ({ tx, clientId }) => {
  const isTimeOver = useTimeDone()
  const [counterDown, setCounterDown] = useState<any>()
  const dispatch = useAppDispatch()
  const { txLoading } = useLoadings()
  const { payed } = useTxPayed()
  const { ronin: fromRonin } = useAuthentication()
  const txData = useTx()

  const findTransaction = async () => {
    dispatch(checkTxThunk({ tx, clientId, uuid: txData.uuid }))
    if (!txLoading) {
      setCounterDown(
        setInterval(async () => {
          dispatch(checkTxThunk({ tx, clientId, uuid: txData.uuid }))
        }, 6000),
      )
    }
  }

  useEffect(() => {
    if (isTimeOver || payed) {
      clearInterval(counterDown)
    }
  }, [payed, isTimeOver, counterDown])

  return <ValidateButton onValidate={findTransaction} validate={txLoading} />
}
