import React, { useState, useEffect } from 'react'
import { Text } from '../../uikit'
import { useAppDispatch } from '../../state'
import { timeDone } from '../../state/user/actions'
import { hasExceedDate } from '../hasExceeedDate'
import { useTxValidUntil } from '../../state/user/hooks'

export const Timer: React.FC = () => {
  const validUntil = useTxValidUntil()
  const dispatch = useAppDispatch()
  const miliseconds = hasExceedDate(validUntil)

  const seconds = miliseconds / 1000
  const secondsAgo = seconds % 60 > 0 ? (seconds % 60).toFixed() : '00'
  const minsAgo = seconds / 60 > 0 ? (seconds / 60).toFixed() : '00'

  const [second, setSecond] = useState(secondsAgo)
  const [minute, setMinute] = useState(minsAgo)

  const [counter, setCounter] = useState(seconds <= 0 ? 0 : seconds)

  useEffect(() => {
    if (miliseconds <= 0) {
      dispatch(timeDone())
    }
  }, [dispatch, miliseconds])

  useEffect(() => {
    let intervalId

    if (counter >= 0) {
      intervalId = setInterval(() => {
        const secondCounter = (counter % 60).toFixed()
        const minuteCounter = Math.floor(counter / 60)

        const computedSecond = String(secondCounter).length === 1 ? `0${secondCounter}` : secondCounter.toString()
        const computedMinute = String(minuteCounter).length === 1 ? `0${minuteCounter}` : minuteCounter.toString()
        setSecond(computedSecond)
        setMinute(computedMinute)

        setCounter((_counter) => _counter - 1)
      }, 1000)
    } else {
      dispatch(timeDone())
    }

    return () => clearInterval(intervalId)
  }, [counter, dispatch])

  return (
    <>
      <Text color="failure">
        {minute}:{second}
      </Text>
    </>
  )
}
